.background {
  min-height: 250px;
  position: relative;
}

.background::after {
  content: "";
  background: url('../../../../assets//images/backgrounds/closing.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.text {
  margin: auto;
  display: grid;
  place-items: center;
  height: 50vh;
  text-align: center;
  width: 250px;
  font-size: 20px;
  color: rgb(247, 248, 248);
  text-shadow: -3px 4px 9px rgba(26, 25, 25, 0.9);
  font-family: Work Sans
}